@import url(https://fonts.googleapis.com/css2?family=Orbitron&family=Permanent+Marker&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Marvel:ital,wght@1,700&display=swap);
.supercard {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  margin-top: 2%;
  -webkit-justify-content: center;
          justify-content: center;
}

.supercard .card {
  margin-right: 10px;
  margin-top: 15px;
  background-color: rgba(0, 0, 0, 0.65);
  color: white;
  border: 1px solid white;
  border-top-width: 1px;
  border-right-width: 1px;
  border-bottom-width: 1px;
  border-left-width: 1px;
  /* max-height: 27vh; */
}

.card > ul > li {
  background-color: rgba(0, 0, 0, 0.65);
  color: white;
  border: 1px solid white;
    border-top-width: 1px;
    border-right-width: 1px;
    border-bottom-width: 1px;
    border-left-width: 1px;
  max-height: 27vh;
}

.first-appearance {
  min-height: 20vh;
  max-height: 20vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}

.hero-name {
  min-height: 23vh;
  background-color: rgba(0, 0, 0, 0.65);
  max-height: 23vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
}

.hero-stats {
  background-color: rgba(0, 0, 0, 0.65);
}

.card-img-top {
  opacity: 0.9;
}

.form {
  margin-top: 50px;
  margin: 0 auto;
}

.form > input {
  background-color: rgba(white, white, white, 0.1);

}

.select {
  background-color: white;
}
/* search container */
.searchContainer {
  display: -webkit-flex;
  display: flex;
  margin-bottom: 4%;
  width: 100%;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 5%;
}

.searchContainer > div > label {
  color: white;
  font-family: "Orbitron", sans-serif !important;
}

.MuiInputBase-input {
  color: white !important;
  font-family: "Orbitron", sans-serif !important;
}

.textField {
  width: 30%;
  font-weight: 500;
  margin: 0 2% !important;
  background-color: rgba(0, 0, 0, 0.95);
  color: white;
}

/* select filters */

.select {
  width: 205px;
  background-color: rgba(0, 0, 0, 0.95);
}

.MuiInputLabel-filled {
  color: white !important;
  font-family: "Orbitron", sans-serif !important;
}

.MuiMenu-paper {
  background-color: rgba(0, 0, 0, 0.75) !important;
}

.MuiMenu-paper > ul {
  color: white !important;
}

.MuiMenu-paper > ul > li {
  font-family: "Orbitron", sans-serif !important;
}

/* Kapow image manipulation */

.kapow-image {
  max-height: 20vh;
}

.load {
  font-family: "Orbitron", sans-serif;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: 2rem;
  color: #ae25c0;
  padding: 1rem;
  font: 60px "permanent marker";
  text-shadow: 6px 6px 6px #000;
}

@media only screen and (max-width: 1024px) {
  .textField {
    width: 50%;
  }

  .selectContainer {
    width: 50%;
  }

  .select {
    width: 50%;
  }
}

@media only screen and (max-width: 600px) {
  .searchContainer {
    -webkit-flex-direction: column;
            flex-direction: column;
    padding: 0 10%;
    -webkit-align-items: center;
            align-items: center;
  }

  .textField {
    width: 100%;
    margin: 0 !important;
  }

  .selectContainer {
    margin: 20px 0;
    width: 100%;
  }

  .select {
    width: 100%;
  }
}

body {
  text-align: center;
  font-family: 'Marvel', sans-serif !important;
  letter-spacing: 1px;
  background-image: url("https://images4.alphacoders.com/108/108584.jpg");
  background-repeat: repeat;
  background-attachment: fixed;
  background-size: cover;
  -webkit-animation: backgroundScroll 30s linear 1;
  -webkit-animation-fill-mode: both;
  -webkit-animation: backgroundScroll linear 30s 1;
          animation: backgroundScroll linear 30s 1;
  animation-fill-mode: both;
  animation-iteration-count: infinite;
  -webkit-animation-iteration-count: infinite;
  animation-direction: alternate-reverse;
  -webkit-animation-direction: alternate-reverse;
}

@keyframes backgroundScroll {
  from {
    background-position: 0 0;
  }

  to {
    background-position: -200px 0;
  }
}

@-webkit-keyframes backgroundScroll {
  from {
    background-position: 0 0;
  }

  to {
    background-position: -200px 0;
  }
}

